import instance from '../utils/request'
import requests from '../utils/requests'

//上传数据
export function upload(data) {
  return instance.post('/project/uploadproject', data)
}
// 获取最新的草稿
export function getdraftbyid(data) {
  return requests.post('/project/getdraftbyid', data)
}

// 免费预约
export function getbpApply(data) {
  return requests.post('/project/bpApply', data)
}
export function vipCardUsage(openId) {
  return requests.get(`/vipCardUsage/detailByEnt?openId=${openId}`)
}